import React, {useState} from 'react'
import { Row,Col,Dropdown,Form } from 'react-bootstrap'
import { Range } from 'rc-slider'
import 'rc-slider/assets/index.css'
import ReactSelect from '../ReactSelect/ReactSelect';
import {propertyType,bathrooms,bedrooms,builtuparea,yearbuilt} from '../ReactSelect/SelectData'
const AdvanceSearchOption=({ClassName,style2,textthm3,btnthm3,index})=>{
    const priceSlider = (value) => {
        document.getElementById("amount").value = "$" + value[0];
        document.getElementById("amount2").value = "$" + value[1];
    }

return(
<div className={ ClassName ? 
                    ClassName ==='home2' ? 
                        'home1-advnc-search '+ClassName+' '+ style2+' mt20'
                        : 'home1-advnc-search ' + ClassName+' '+ style2
            : 'home1-advnc-search '
 }
>
<ul className="h1ads_1st_list mb0">
    <li className="list-inline-item">
        <div className="form-group">
            <input type="text" className="form-control" id={`${index}_exampleInputName1`} placeholder="Enter keyword..." />
        </div>
    </li>
    <li className="list-inline-item">
        <div className="search_option_two " >
            <div className="candidate_revew_select">
                    <ReactSelect data={propertyType} tickColor='#ff5a5f' style2={textthm3} />
            </div>
        </div>
    </li>
    <li className="list-inline-item">
        <div className="form-group">
            <input type="text" className="form-control" id={`${index}exampleInputEmail`} placeholder="Location" />
            <label htmlFor={`${index}exampleInputEmail`}><span className="flaticon-maps-and-flags" /></label>
        </div>
    </li>
    <li className="list-inline-item">
        <div className={"small_dropdown2 "+ ClassName}>
            <Dropdown>
                <Dropdown.Toggle variant="custom" id={`${index}_prncgs`} className="dd_btn prncgsBtn">
                    <span>Price</span>
                    <span className="fa fa-angle-down float-right mt10"></span>
                </Dropdown.Toggle>

                <Dropdown.Menu className={ClassName+" dd_content2"}>
                    <div className="pricing_acontent">
                        <input type="text" id={`${index}amount`} className="amount" placeholder="$3000" /> 
                        <input type="text" id={`${index}amount2`} className="amount2" placeholder="$18000" />
                        <Range min={2000} max={18990} defaultValue={[3000, 18000]} onChange={priceSlider} />
                    </div>
                </Dropdown.Menu>
            </Dropdown>
        </div>
    </li>
    <li className="custome_fields_520 list-inline-item">
        <div className="navbered">
            <div className={"mega-dropdown " + ClassName}>
                <Dropdown>
                    <Dropdown.Toggle variant="custom" className="droptnCustom" bsPrefix="custom">
                            Advanced <i className="flaticon-more droptnIcon"></i>
                    </Dropdown.Toggle>

                    <Dropdown.Menu className={ClassName+" dropdown-content"}>
                        <Row className="p15">
                            <Col lg={12}><h4 className="text-thm3">Amenities</h4></Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck1`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck1`}>Air Conditioning</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck2`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck2`}>Lawn</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck3`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck3`}>Swimming Pool</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck4`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck4`}>Barbeque</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck5`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck5`}>Microwave</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck6`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck6`}>TV Cable</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck7`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck7`}>Dryer</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck8`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck8`}>Outdoor Shower</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck9`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck9`}>Washer</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck10`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck10`}>Gym</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck11`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck11`}>Refrigerator</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck12`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck12`}>WiFi</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck13`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck13`}>Laundry</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck14`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck14`}>Sauna</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id={`${index}_customCheck15`} />
                                            <label className="custom-control-label" htmlFor={`${index}_customCheck15`}>Window Coverings</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                        </Row>
                        <Row className="p15 pt0-xsd">
                            <Col xl="10" lg="11">
                                <ul className="apeartment_area_list mb0">
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select" style={{zIndex:'2222'}}>
                                        <ReactSelect data={bathrooms} tickColor='#ff5a5f' />
                                        </div>
                                    </li>
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={bedrooms} tickColor='#ff5a5f' />

                                        </div>
                                    </li>
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select">
                                           <ReactSelect data={yearbuilt} tickColor='#ff5a5f' />

                                        </div>
                                    </li>
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={builtuparea} tickColor='#ff5a5f' />
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col xl="2" lg="1"></Col>
                        </Row>
                    </Dropdown.Menu>
                </Dropdown>
            </div>
        </div>
    </li>
    <li className="list-inline-item">
        <div className={style2 ? "search_option_button style2" :"search_option_button"}>
            <button type="submit" className={btnthm3 ? "btn btn-thm3": "btn btn-thm"}>Search</button>
        </div>
    </li>
</ul>               
</div>)
}
export default AdvanceSearchOption